.App {
  text-align: center;
  background-image: url('./img/fon.jpg');
  width: 100%;
  height: 100vh;
  background-size: cover;
  position: relative;
}


.container-title {
  height: 100px;
  width: 500px;
  color: rgb(4, 0, 0);
  border-radius: 15px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.background-container {
  background-color: rgb(206, 203, 203);
  opacity: 0.6;
  height: 100px;
  width: 500px;
  position: absolute;
  border-radius: 10%;
}

.USOVA {
  position: absolute;
  color: #000000;
  text-shadow:
    0 0 7px #fff,
    0 0 10px #fff,
    0 0 21px #fff,
    0 0 42px #0fa,
    0 0 82px #0fa,
    0 0 92px #0fa,
    0 0 102px #0fa,
    0 0 151px #0fa;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.img {
  width: 700px;
  height: 500px;
  margin-left: 1px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}