.contact-container {
    text-align: center;
    background-image: url('../../pages/img/fon.jpg');
    width: 100%;
    height: 100vh;
    background-size: cover;
    position: relative;
}


.form {
    background-color: white;
    height: 200px;
    border-radius: 25px;
    width: 400px;
    display: flex;
    flex-direction: column;
    width: 500px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    padding: 50px;
    opacity: 90%;
}

.button-form {
    width: 150px;
}

.input-form {
    margin-bottom: 50px;
}

@media screen and (min-width: 600px) and (max-width: 1023px) {
    .form {
        width: 300px;
        height: 200px;

    }
}

@media screen and (min-width: 420px) and (max-width: 599px) {
    .form {
        width: 200px;
        height: 180px;

    }
}